li.route-data {
  margin: 10px 0;

  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  &:hover {
    -webkit-transform: scale(1.01, 1.01);
    transform: scale(1.01, 1.01);
    &::after {
      opacity: 1;
    }
  }
  &:first-child {
    margin-top: -60px;
  }
}

.routedata-am, .routedata-pm {
  list-style: none;
  margin: -30px 0;
}

img.train-icon {
  margin-right: 10px;
  top: 2px;
  position: relative;
}
