.ptr--pull {
  transition: none;
}
.ptr--top {
  touch-action: pan-x pan-down pinch-zoom;
}
.ptr--release .ptr--icon {
  transform: rotate(180deg);
}

.ptr--ptr {
  background-color: #333;

  box-shadow: inset 0 -3px 5px rgba(0, 0, 0, 0.12);
  pointer-events: none;
  font-size: 0.85em;
  font-weight: bold;
  top: 0;
  height: 0;
  transition: height 0.3s,
  min-height 0.3s;
  text-align: center;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  align-content: stretch;

  .ptr--box {
    padding: 10px;
    flex-basis: 100%;
    .ptr--content {
      .ptr--text {
        color: white;
      }
      .ptr--icon {
        color: white;
        transition: transform .3s;
      }
    }
  }
}
